/**
 *


 const rxData = Roxy({
        title: '',
        body: '',
    });

 function setTo(id, context) {
        const elem = query('#' + id);
        inject(elem, context);
    }

 rxData.event((key, values)=>{
        setTo(key, values);
    });

 Timer.timeout(() => {
        rxData.dispatch('title', 'Hello friend');
        rxData.dispatch('body', 'Do you happy now?');
    }, 2000);

 Timer.timeout(() => {
        rxData.dispatch('body', 'No yes ofcourse');
    }, 4000);
 *
 *
 * @param payload
 * @returns {{proxy: *, set(*=, *=): *, get(*): *, action(*=, *=): *, fill(*=, *=): *}|Roxy|{}|null|boolean}
 * @constructor
 */
import copy from "../static/copy";

const Roxy = function (payload)
{
    const actions = {default:null};

    const proxy = new Proxy(payload, {
        get(obj, prop) {
            return prop in obj ? obj[prop] : null;
        },
        set(obj, prop, value) {
            if (actions[prop] && typeof actions[prop] === "function")
                actions[prop].call({}, prop, value, copy(proxy));

            if (typeof actions.default === "function")
                actions.default.call({}, prop, value, copy(proxy));

            obj[prop] = value;
            return true;
        }
    });

    return {

        /**
         * older name: see|set?
         * @param key
         * @param payload
         * @returns {*}
         */
        dispatch (key, payload) {
            let result;

            if (typeof key === "function" && !payload) {
                result = key.call({}, proxy);
                if (result)
                    this.fill(result)

            } else if (typeof payload === "function") {
                result = payload.call({}, proxy[key]);
                if (result)
                    proxy[key] = result;

            } else
                proxy[key] = payload;

            return this;
        },

        /**
         *
         * older name: action
         * @param key
         * @param callback
         * @returns {*}
         */
        event (key, callback) {
            if (typeof key === "function" && !callback) {
                actions.default = key;
            } else
                actions[key] = typeof callback === "function" ? callback : null;

            return this;
        },

        get (key) {
            return key ? proxy[key] : copy(proxy);
        },

        fill (payload, to) {
            Object.keys(payload).forEach((key) => {
                if (to) {
                    if (!proxy[to])
                        proxy[to] = {};
                    proxy[to][key] = payload[key]
                } else {
                    proxy[key] = payload[key]
                }
            });

            return this;
        },

        proxy,
    };
};


export default Roxy;
