
const Scene = function (config = {steps: []}) {
    const root = {
        iteration: 0,
        current: 0,
        pause: true,
        start() {
            root.current = 0;
            root.play();
            return root;
        },
        stop() {
            root.pause = true;
            return root;
        },
        play() {
            root.pause = false;
            return root;
        },
        next() {
            return root;
        },
    }

    return root;
};

export default Scene;
